import { Injectable } from '@angular/core';
import { AuthService } from './auth.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Post } from './post';

@Injectable()
export class PostService {

    posts: Post[] = [
        new Post('Test 1', 'Test Category', '25.04.2019'),
        new Post('Test 2', 'Test Category', '26.04.2019')
    ]

    headers = new HttpHeaders().set('Content-Type', 'application/json');

    constructor(private http: HttpClient, private authService: AuthService) {}

    getAllPosts() {
        const token = this.authService.getToken();
        return this.http.get<any[]>(`https://blogeriii.firebaseio.com/posts.json?auth=${token}`, {
            headers: this.headers
        });
    }

    savePosts(posts: any[]) {
        const token = this.authService.getToken();
        return this.http.put(`https://blogeriii.firebaseio.com/posts.json?auth=${token}`, posts, {
            headers: this.headers
        });
    }

    getPosts() {
        return this.posts;
    }


}