import { Component, OnInit, Input } from '@angular/core';
import { trigger, state, style, transition, animate, keyframes } from '@angular/animations';
import { FormGroup, FormControl, Validators, NgForm } from '@angular/forms';
import { PostService } from '../post.service';


@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css'],
  animations: [
    trigger('postAnim', [
      state('insert', style({
        'background-color': 'white',
        'text-decoration': 'line-through',
        'text-decoration-color': 'transparent',
        opacity: 0,
        transform: 'translateX(0)'
      })),
      transition('void => *', [
        animate(1300, keyframes([
          style({
            opacity: 0,
            transform: 'translate(-100px)',
            offset: 0
          }),
          style({
            opacity: 0.5,
            transform: 'translate(-70px)',
            offset: 0.3
          }),
          style({
            opacity: 0.8,
            transform: 'translate(-40px)',
            offset: 0.8
          }),
          style({
            opacity: 1,
            transform: 'translate(0)',
            offset: 1
          })
        ]))
      ]),
      transition('* => void', [
        animate(1500, keyframes([
          style({
            'background-color': 'white',
            'text-decoration': 'line-through',
            'text-decoration-color': 'transparent',
            offset: 0
          }),
          style({
            'background-color': '#fc8387',
            'text-decoration': 'line-through',
            'text-decoration-color': 'white',
            offset: 0.6
          }),
          style({
            opacity: 0,
            transform: 'translateX(100px)',
            offset: 1
          })
        ])),

      ])
    ]) 
  ]
})





export class PostsComponent implements OnInit {
  posts: { id: number, title: string, category: string, datePosted: string }[] = [];
  
  addForm: FormGroup;
  
  idX: number;

  state = "normal";

  constructor(private postService: PostService) { }

  

  ngOnInit() {
    this.addForm = new FormGroup({
      'title': new FormControl(null, Validators.required),
      'category': new FormControl(null, [Validators.required]),
      'date': new FormControl(null, [Validators.required])
    });
    
    this.postService.getAllPosts()
      .subscribe(
        posts => {
            
            this.posts = posts;
        }
      )
  }

  deletePost(id: number) {
    this.posts = this.posts.filter(
      posts => posts.id != id
    );
    this.postService.savePosts(this.posts)
      .subscribe();
  }

  changePost(form: NgForm){
    
    for(let post of this.posts){
      if(post.id === this.idX){
        post.title = form.value.title;
        post.category = form.value.category;
        post.datePosted = form.value.date
      }
    }
    this.postService.savePosts(this.posts)
        .subscribe()
      form.reset();
  }


  submit() {
    const posts = this.posts ? this.posts : [];

    if(posts.length === 0){
      
      let newPost = {
        id: 0,
        title: this.addForm.value.title,
        category: this.addForm.value.category,
        datePosted: this.addForm.value.date
      }
      posts.push(newPost);
      
    } else {
      let newPost = {
        id: posts[posts.length - 1].id + 1, 
        title: this.addForm.value.title,
        category: this.addForm.value.category,
        datePosted: this.addForm.value.date
      };
      posts.push(newPost);
      
    }
    this.posts = posts;
    this.postService.savePosts(this.posts)
        .subscribe()
    this.addForm.reset();
  }

  

}
