import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Blogger } from 'src/app/blogger';
import { Observable } from 'rxjs';
import { BloggersService } from 'src/app/bloggers.service';
import { Store } from '@ngrx/store';
import { NgForm } from '@angular/forms';
import * as BloggersAction from '../../store/bloggers.actions';
import { trigger, state, style, transition, animate } from '@angular/animations';

@Component({
  selector: 'app-blogger',
  templateUrl: './blogger.component.html',
  styleUrls: ['./blogger.component.css'],
  animations: [
    trigger('bloggerState', [
      state('normal', style({
        transform: 'scale(1)'
      })),
      state('bigger', style({
        transform: 'scale(1.5)'
      })),
      transition('normal <=> bigger', animate(300))
    ])
  ]
})
export class BloggerComponent implements OnInit {

  state = 'normal';
  
  

  @Input() blogger: Blogger;
  @Input() index: number;
  modalIndex: number;
  

  bloggersState: Observable<{ bloggers: Blogger[]}>;

  constructor(private bloggersService: BloggersService, private store: Store<{bloggers: {bloggers: Blogger[]}}>) { }

  ngOnInit() {
    this.bloggersState = this.store.select('bloggers');
    //this.modalIndex = this.index;
    
  }

  

  setIndex() {
    
    this.modalIndex = this.index;
    console.log(this.modalIndex);
  }
  


  changeBlogger(form: NgForm) {
    // const forward = {
    //   index: this.modalIndex,
    //   blogger: new Blogger(form.value.name, form.value.email, form.value.about, form.value.img)
    // }
    // this.store.dispatch(new BloggersAction.EditBlogger(forward));
    console.log(this.modalIndex);
    this.store.dispatch(new BloggersAction.EditBlogger({
      index: this.index,
      blogger: new Blogger(form.value.name, form.value.email, form.value.about, form.value.img)
      
    }));
    this.bloggersState.subscribe(
      blog => {
        this.bloggersService.saveBlogger(blog.bloggers).subscribe();
      }
    );
    //console.log(this.index)
    form.reset();
  }

  deleteBlogger(){
    this.store.dispatch(new BloggersAction.DeleteBlogger(this.index));
    this.bloggersState.subscribe(
      blog => {
        this.bloggersService.saveBlogger(blog.bloggers).subscribe();
      }
    )
  }

  resizeBlogger() {
    this.state === 'normal' ? this.state = 'bigger' : this.state = 'normal';
  }

  

}
