import * as BloggersActions from './bloggers.actions';
import { Blogger } from '../blogger';
import {BloggerComponent} from '../bloggers/blogger/blogger.component'

const initialState = {
    bloggers: [
        //new Blogger('Blogger 1', 'blogger1@gmail.com','About Blogger 1', 'https://images.pexels.com/photos/1236701/pexels-photo-1236701.jpeg?cs=srgb&dl=4k-wallpaper-blur-buildings-1236701.jpg&fm=jpg' )
    ]
}

export function bloggersReducer(state = initialState, action: BloggersActions.Actions) {
    switch(action.type) {
        case BloggersActions.ADD_BLOGGER:
            const blogg = state.bloggers ? state.bloggers : [];
            //console.log(blogg)
            return {
                // ...state,
                // bloggers: [...state.bloggers, action.payload]
                ...blogg,
                bloggers: [...blogg, action.payload]
            };
        case BloggersActions.DELETE_BLOGGER:
            const oldBloggers = [...state.bloggers];
            oldBloggers.splice(action.payload, 1);
            return {
                ...state,
                bloggers: oldBloggers
            };
        case BloggersActions.EDIT_BLOGGER:
            // console.log(state.bloggers[action.payload.index]);
            // const blogger = state.bloggers[action.payload.index];
            // const editedBlogger = {
            //     ...blogger,
            //     ...action.payload.blogger
            // };
            // const beforeEditingBloggers = [...state.bloggers];
            // beforeEditingBloggers[action.payload.index] = editedBlogger;
            // console.log(action.payload.index);
            // return {
            //     ...state,
            //     bloggers: beforeEditingBloggers
            // }
            const beforeEditingBloggers = [...state.bloggers];
            beforeEditingBloggers[action.payload.index] = action.payload.blogger;
            console.log(action.payload.index);
            return {
                ...state,
                bloggers: beforeEditingBloggers
            };
        case BloggersActions.ADD_BLOGGERS:
            return {
                ...state,
                bloggers: action.payload
                // ...state,
                // bloggers: [...state.bloggers, ...action.payload]
            }
        default:
            return state;
    }
}

