import { Component, OnInit, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { Store } from '@ngrx/store';
import { Blogger } from 'src/app/blogger';
import { NgForm } from '@angular/forms';
import { BloggersService } from 'src/app/bloggers.service';
import * as BloggersAction from '../../store/bloggers.actions';

@Component({
  selector: 'app-addblogger',
  templateUrl: './addblogger.component.html',
  styleUrls: ['./addblogger.component.css']
})
export class AddbloggerComponent implements OnInit {

  bloggersState: Observable<{bloggers: Blogger[]}>;
  @ViewChild('f') form: NgForm;

  constructor(private bloggersService: BloggersService, private store: Store<{bloggers: { bloggers: Blogger[]}}>) { }

  ngOnInit() {
    this.bloggersState = this.store.select('bloggers');
  }

  addBlogger() {
    this.store.dispatch(new BloggersAction.AddBlogger(
      new Blogger(this.form.value.name, this.form.value.email, this.form.value.about, this.form.value.img)
    ));
    this.bloggersState.subscribe(
      (blog) => {
        this.bloggersService.saveBlogger(blog.bloggers).subscribe();
      }
    );
    this.form.reset()
  }

}
