import { Component, OnInit, Output } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { Blogger } from '../blogger';
import { BloggersService } from '../bloggers.service';
import { Store } from '@ngrx/store';
import { NgForm } from '@angular/forms';
import * as BloggersAction from '../store/bloggers.actions';

@Component({
  selector: 'app-bloggers',
  templateUrl: './bloggers.component.html',
  styleUrls: ['./bloggers.component.css']
})
export class BloggersComponent implements OnInit {

  //bloggers: Blogger[];
  
  //index: number;
  bloggersState: Observable<{bloggers: Blogger[]}>;

  constructor(private bloggersService: BloggersService, private store: Store<{bloggers: {bloggers: Blogger[]}}>) {}

  ngOnInit() {
    this.bloggersState = this.store.select('bloggers'); 
    
  }

  addBlogger(form: NgForm) {
    this.store.dispatch(new BloggersAction.AddBlogger(
      new Blogger(form.value.name, form.value.email, form.value.about, form.value.img)
    ));
    this.bloggersState.subscribe(
      blog => {
        this.bloggersService.saveBlogger(blog.bloggers).subscribe();
      }
    );
    form.reset();
  }

}
