import { Component, OnInit } from '@angular/core';
import { BloggersService } from '../bloggers.service';
import { Store } from '@ngrx/store';
import { Blogger } from '../blogger';
import * as BloggersAction from '../store/bloggers.actions';
import { Observable } from 'rxjs';
import { ViewportScroller } from '@angular/common';


@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {

  constructor(private bloggersService: BloggersService, private store: Store<{bloggers: {bloggers: Blogger[]}}>, private viewPort: ViewportScroller) { }
  //bloggersState: Observable<{bloggers: Blogger[]}>;

  ngOnInit() {
    //this.bloggersState = this.store.select('bloggers'); 
    this.getAllBloggers();
  }

  getAllBloggers() {
    this.bloggersService.getAllBloggers()
      .subscribe(
        bloggers => {
          this.store.dispatch(new BloggersAction.AddBloggers(bloggers));
        }
      );
  }

}
